<template>
  <div class="mobile-container">
    <Header/>

    <div class="m-content">
      <div class="m-pos-bg">
        <img src="@/assets/m/bg.png" alt="" class="bg-img">
        <div class="m-pos-con">
          <div class="pos-con-img" style="justify-content: space-between">
            <p>
              <img src="@/assets/img/qr.png" alt="" style="vertical-align: middle;margin-right: 0;">
              {{ $t('lang.address_title') }}
            </p>
            <p>
              <router-link tag="a" :to="{path:'/mtokenone',query:{address: token}}" style="color: #ffffff;"><img
                src="@/assets/img/erc.png" style="width: 26px;height: 26px;vertical-align: middle;margin-right: 0;"
                alt="">ERC20
              </router-link>
            </p>
          </div>
          <div class="m-nodeid">{{ subStr(address) }}</div>
        </div>
      </div>
      <div class="m-con addr-con">
        <div class="qr-wrap">
          <div :id="addressData.address + 'button'" class="m-qrdata"></div>
        </div>
        <div class="m-head">
          <div class="m-item">
            <div for="" class="item-lebel">{{ addressData.received }} GKC</div>
            <p>{{ $t('lang.address_detail1') }}</p>
          </div>
          <div class="m-item">
            <div for="" class="item-lebel">{{ addressData.sent }} GKC</div>
            <p>{{ $t('lang.address_detail2') }}</p>
          </div>
          <div class="m-item">
            <div for="" class="item-lebel">{{ addressData.balance }} GKC</div>
            <p>{{ $t('lang.address_detail3') }}</p>
          </div>
        </div>
      </div>
      <div class="m-tips">{{ $t('lang.address_dpos_title') }}</div>
      <div class="m-con addr-con">
        <div class="m-head">
          <div class="m-item">
            <div for="" class="item-lebel">{{ userData.entrust }} GKC</div>
            <p>{{ $t('lang.address_pos1') }}</p>
          </div>
          <div class="m-item">
            <div for="" class="item-lebel">{{ userData.incoming }} GKC</div>
            <p>{{ $t('lang.address_pos2') }}</p>
          </div>
          <div class="m-item">
            <div for="" class="item-lebel">{{ userData.unpaid }} GKC</div>
            <p>{{ $t('lang.address_pos3') }}</p>
          </div>
        </div>
      </div>
      <div class="m-tips">{{ $t('lang.address_trans_title') }}</div>
      <div class="m-con m-bg">
        <div class="m-con-log" v-for="(item, index) in transData" :key="index" :prop="item.prop">
          <div class="log-title">
            <img src="@/assets/img/exchange.png" alt="">
            <div class="log-link">
              <router-link tag="a" :to="{path:'/mtx',query:{txid: item.txid}}">{{ item.txid }}</router-link>
            </div>
          </div>
          <div
            style="display: flex;font-size: 16px;color: #888888;margin-top: 20px; flex-direction: column;justify-content: space-between;">
            <p style="margin-bottom: 10px;"><label for="" style="color: #005DA6;margin-right: 10px;">{{ $t('lang.address_trans1') }}</label>{{
              item.height }}</p>
            <p><label for="" style="color: #005DA6;margin-right: 10px;">{{ $t('lang.address_trans2') }}</label>{{ filterDate(item.time) }}
            </p>
          </div>
          <div class="log-con">
            <div class="log-con-title">{{ $t('lang.address_trans3') }}</div>
            <div class="log-con-info">
              <div class="info-item" v-for="(item2, index2) in item.from_array" :key="index2">
                <div class="item-child" v-for="(item3, index3) in item2" :key="index3">
                  <div class="item-form"><span>{{ item3 }} GKC</span> from</div>
                  <div class="item-details">
                    <img src="@/assets/img/qr.png" alt="">
                    <router-link v-if=isGoto(index3) tag="a" :to="{path:'/maddress',query:{address: index3}}">{{ index3 }}</router-link>
<!--                    <a v-if=isGoto(index3) :href="{path:'/maddress',query:{address: index3}}">{{ index3 }}</a>-->
                    <a v-else href="javascript:void(0)">{{ index3 }}</a>
<!--                    <router-link tag="a" :to="{path:'/maddress',query:{address: index3}}">{{ index3 }}</router-link>-->
                  </div>
                </div>
              </div>
              <div class="log-con-img"><img src="@/assets/m/down.png" alt=""></div>
              <div class="info-item" v-for="(item4, index4) in item.to_array" :key="index4">
                <div class="item-child" v-for="(item5, index5) in item4" :key="index5">
                  <div class="item-form" v-if="index5 === 'unknown'"><span>Empty</span></div>
                  <div class="item-form" v-else-if="index5 === 'Non-standard script'"><span>Cointract</span></div>
                  <div class="item-form" v-else><span>{{ item5 }} GKC</span> To</div>
                  <div class="item-details" v-if="index5 === 'unknown'">
                    <img src="@/assets/img/unkown.png" alt="">
                    <p v-if="index === 0" style="display: inline-block">Coinbase</p>
                    <p v-else style="display: inline-block">Coinstake</p>
                  </div>
                  <div class="item-details" v-else-if="index5 === 'Non-standard script'">
                    <img src="@/assets/img/unkown.png" alt="">
                    <p style="display: inline-block">{{ index5 }}</p>
                  </div>
                  <div class="item-details" v-else>
                    <img src="@/assets/img/qr.png" alt="">
                    <router-link v-if=isGoto(index5) tag="a" :to="{path:'/maddress',query:{address: index5}}">{{ index5 }}</router-link>
<!--                    <a v-if=isGoto(index5) :href="{path:'/maddress',query:{address: index5}}">{{ index5 }}</a>-->
                    <a v-else href="javascript:void(0)">{{ index5 }}</a>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div class="block_page" style="margin-top: -1rem">
          <pageSize :page-data="pageData" :layout="`prev, pager, next,jumper`" @currentChange="currentChange"
                    @sizeChange="sizeChange"/>
        </div>

      </div>
    </div>
  </div>
</template>
<script>
  import Header from '@/components/MHeader'
  import Footer from '@/components/MFooter'
  import pageSize from '@/components/MPage'
  import dateFns from 'date-fns'
  import QRCode from 'qrcodejs2'

  export default {
    components: {
      Header,
      Footer,
      pageSize
    },
    data() {
      return {
        addressData: [],
        transData: [],
        address: '',
        userData: [],
        pageData: {
          currentPage: 1,
          pageSize: 10,
          total: 0
        },
        token: ""
      }
    },
    created() {
      this.address = this.$route.query.address;
    },
    computed: {},
    watch: {
      $route() {
        this.address = this.$route.query.address;
        this.getBalanceByAddress(this.address)
        this.getBlockInfoByAddress(this.address)
        this.getUserData(this.address)
      }
    },
    mounted() {
      this.getBalanceByAddress(this.address)
      this.getUserData(this.address)
      this.getBlockInfoByAddress(this.address)
    },
    methods: {
      sizeChange(size) {
        this.pageData.currentPage = 1;
        this.pageData.pageSize = size
      },
      currentChange(val) {
        this.pageData.currentPage = val;
        this.getBlockInfoByAddress(this.address)
      },
      getBalanceByAddress(address) {
        this.$http.post("api/block/balance", {address: address}).then(res => {
          if (res.data.code === 200) {
            this.addressData = res.data.data.address
            setTimeout(() => {
              $(".qrdata").html("")
              this.qrcode(70, 70, this.addressData.address + 'button', this.addressData.address)
            }, 500);

          }
        })
      },
      getBlockInfoByAddress(address) {
        this.$http.post("api/block/address", {address: address, p: this.pageData.currentPage}).then(res => {
          if (res.data.code === 200) {
            this.transData = res.data.data.list
            this.pageData.currentPage = res.data.data.page.current_page
            this.pageData.total = res.data.data.page.total
            this.pageData.pageSize = res.data.data.page.page_size
            this.token = res.data.data.token
          }
        })
      },
      qrcode(width, height, id, address) {
        new QRCode(id, {
          text: address,
          width: width,
          height: height,
          colorDark: '#333333',
          colorLight: '#ffffff',
          correctLevel: QRCode.CorrectLevel.L
        })
      },
      getUserData(address) {
        this.$http.post("api/node/user", {address: address}).then(res => {
          if (res.data.code === 200) {
            this.userData = res.data.data
          }
        })
      },
      filterDate(value, type) {
        if (!value) return ''
        if (value > 0) {
          value = value * 1000
        }
        const filterType = type || 'YYYY-MM-DD HH:mm:ss Z'
        return dateFns.format(value, filterType)
      },
      subStr(str){
        if (str === "") {
          return ""
        }
        if (str.length === 34) {
          return str
        }
        str = str.substr(0, 50);
        str += "...";
        return str
      },
      isGoto(str) {
        if (str.indexOf("G") === 0 && str.length === 34){
          return true;
        }else{
          return false;
        }
      }
    }
  }
</script>
<style scoped>
  .m-pos-bg {
    text-align: left;
    position: relative;
    margin-bottom: .8rem;
  }

  .m-pos-bg .bg-img {
    width: 100%;
    height: 8rem;
  }

  .m-pos-con {
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    color: #ffffff;
  }

  .pos-con-img {
    margin: 1rem;
    display: flex;
    align-items: center;
  }

  .pos-con-img img {
    width: 2rem;
    height: 2rem;
    margin-right: .8rem;
  }

  .m-nodeid {
    word-wrap: break-word;
    margin: 0 5rem 0 3rem;
  }

  .m-btn-wrap {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 1rem;
  }

  .m-btn {
    width: 100px;
    font-size: 12px;
    text-align: center;
    background: #F0F0F0;
    line-height: 32px;
    border-radius: 4px;
  }

  .m-content {
    padding: 1rem;
    background: #E6E9EE;
  }

  .m-head {
    background: #ffffff;
  }

  .m-head .m-item {
    text-align: center;
    padding: 1rem 0;
  }

  .m-item p {
    color: #888888;
    font-size: 12px;
  }

  .item-lebel {
    font-size: 17px;
    margin-bottom: .5rem;
  }

  .m-tips {
    padding: .7rem;
    text-align: left;
  }

  .m-con {
    background: #ffffff;
  }

  .head-btn {
    text-align: center;
    padding: 1.5rem 0;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .head-btn .to-details {
    width: 10rem;
    line-height: 3rem;
    background: rgba(0, 93, 166, 1);
    opacity: 1;
    border-radius: 4px;
    color: #ffffff;
  }
</style>



